.nav__container {
  display: flex;
  gap: 50%;
  align-items: center;
  width: 100%;
  height: 67px;
  background-color: #ffb200;
}

.nav__container > span {
  padding-left: 17%;
  text-transform: uppercase;
  font-style: italic;
  user-select: none;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  /* identical to box height */
  text-align: center;
  letter-spacing: -0.3px;
  color: #ffffff;
}

.nav__container > a > button {
  background: #ffffff;
  border-radius: 42px;
  font-size: 18px;
  border: none;
  padding: 7px 20px;
  cursor: pointer;
}

@media screen and (max-width: 499px) {
  .nav__container {
    background-color: #fff;
    padding-top: 3%;
    gap: 38%;
  }

  .nav__container > span {
    padding-left: 1.7em;
    font-size: 20px;
    color: #f59428;
  }

  .nav__container > a > button {
    border: 1px solid #797979;
    font-size: 14px;
  }
}
